#inputadd{
    width: 90%;
    margin-left: 2%;
    text-align: center;
    margin-bottom: 2%;
}

label{
    font-weight: bold;
}

input{
    margin-top: 2%;
    width: 60%;
    height: 5vh;
    padding: 10px;
    border-radius: 20px;
    outline: none;
}

input::placeholder{
    color: #001e1d;
}

@media screen and (max-width:500px){
    label{
        font-size: 0.8em;
    }
    input{
        width: 80%;
    }
  }